<script>
import { getData, exportFile } from "common/utils/public_function/commonUtils";
import customeReceiptDetailMinxin from "./customeReceiptDetail";
export default {
  name: "customCustomerbillCustomeReceiptDetail",
  mixins: [customeReceiptDetailMinxin],
  data() {
    return {
      totalLoading: false, // 合计行加载
      billArr: [], // 核销类型多选
      accountsTypeArr: [], // 往来类型多选
      listShow: false, // 控制列表是否显示
      auditorTimeValue: [], // 审核日期
      businessTimeValue: [], // 业务日期
      loading:false,
    };
  },

  components: {},

  computed: {
    customAccountypeList() {
      let _accountypeList = this.accountypeList;
      if (_accountypeList.length > 0) {
        _accountypeList.push({
          accounts_type_id: 4,
          accounts_type_name: "预收冲应收",
        });
      }
      return _accountypeList;
    },
  },

  methods: {
    // 接口 请求列表数据
    portGetList(params = {}) {
      params.TableData = this.TableData;
      params.API = "getCustomReceiptBillDetailList";
      params.vm = this;
      this.portGetTotalSum(params.params);
      getData(params, "", (res) => {
        if (res.data.code == 200) {
          let _data = res.data.data;
          this.$set(this.TableData, "user_name", _data.user_name);
          this.$set(this.TableData, "now_time", _data.now_time);
          // 处理表格合并（用于处理单元格）
          // let saveIndex = -1; // 保存要储存的index
          // let count = 1; // 记录要合并的行
          // this.TableData.tableData.forEach((item, index, arr) => {
          //   if (!item.company_name && index > 0) {
          //     if (saveIndex == -1) saveIndex = index - 1;
          //     count += 1;
          //     arr[saveIndex].rowspan = count;
          //     arr[index].rowspan = 0;
          //   } else {
          //     saveIndex = -1;
          //     count = 1;
          //   }
          // });
          this.listShow = true; // 显示列表
          this.$nextTick(() => {
            this.updateFooter(); // 刷新表格table footer
          });
          // this.$nextTick(() => {
          //   let _table = this.$refs.tableName,
          //     _table_sum = this.$refs.table_sum;
          //   if (_table && _table_sum) {
          //     const elTable1 = _table.$el.getElementsByClassName(
          //       "el-table__body-wrapper"
          //     )[0];
          //     elTable1.addEventListener("scroll", function() {
          //       _table_sum.scrollLeft = this.scrollLeft;
          //     });
          //   }
          // });
        }
      });
    },
    // 接口获取合计
    portGetTotalSum(params) {
      this.totalLoading = true; // 开启加载
      this.$axMethod
        .getCustomReceiptBillDetailSum(params)
        .then(({ data }) => {
          this.totalLoading = false; // 关闭加载
          let all_bill_receipt_price = 0,
            all_total_price = 0;
          if (data.code == 200) {
            all_bill_receipt_price = this.$options.filters["priceFormatNone"](
              data.data.all_bill_receipt_price
            );
            all_total_price = this.$options.filters["priceFormatNone"](
              data.data.all_total_price
            );
          } else if (data.code == 403) {
            all_bill_receipt_price = "无权限";
            all_total_price = "无权限";
          }
          this.$set(
            this.TableData,
            "all_bill_receipt_price",
            all_bill_receipt_price
          );
          this.$set(this.TableData, "all_total_price", all_total_price);
          this.$nextTick(() => {
            this.updateFooter(); // 刷新表格table footer
          });
        })
        .catch((error) => {
          this.totalLoading = false; // 关闭加载
        });
    },
    // 接口 导出
    exportOrder(scheduleType) {
      //导出
      let arg = {
        params: {
          export_type: scheduleType,
          ...this.params,
        },
        API: "exportCustomReceiptBillDetailListUrl",
      };
      exportFile(arg);
    },
    module_handleSizeChange(val) {
      // 分页操作
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      this.portGetList(this.searchParams);
    },
    module_handleCurrentChange(val) {
      // 分页操作
      Object.assign(this.searchParams.params, {
        page: val,
      });
      this.portGetList(this.searchParams);
    },
    // 字段格式化
    formatterValue({ cellValue, row, column }) {
      let value = this.$options.filters["priceFormatNone"](cellValue);
      return value;
    },
    // 表头 组织下拉change 事件
    companyChange(val) {
      // 清空相关联字段
      let fieldArr = ["customer_id", "business_id", "department_id"];
      fieldArr.forEach((key) => {
        this.$set(this.params, key, "");
      });
    },
    // 表头 往来类型change 事件
    accountsTypeChange(sectionArr) {
      this.params.accounts_type_id = this.accountsTypeArr.toString();
    },
    billTypeChange(sectionArr) {
      this.params.bill_type = this.billArr.toString();
    },
    // 表头 业务日期 change 事件
    timeChange(sectionArr, type) {
      // type 1 表示处理业务日期 其它默认处理审核日期
      let _startTime = "",
        _endTime = "";
      if (sectionArr && sectionArr.length >= 2) {
        try {
          _startTime = sectionArr[0];
          _endTime = sectionArr[1];
        } catch (error) {
          console.log("程序出错请检查。。。");
        }
      }
      switch (type) {
        case 1:
          this.params.business_start_time = _startTime;
          this.params.business_end_time = _endTime;
          break;
        default:
          this.params.auditor_start_time = _startTime;
          this.params.auditor_end_time = _endTime;
      }
    },
    // 清除字段
    clickClear(obj, keyArr) {
      keyArr.forEach((key) => {
        obj[key] = "";
      });
    },
    // 表头 客户下拉change 事件
    getCustomer(val) {
      if (val) {
        if (!this.params.company_id) return;
        this.$axMethod
          .getCustomerInfoV2({
            company_id: this.params.company_id,
            customer_id: val,
          })
          .then(({ data }) => {
            if (data.code == 200) {
              // 如果有业务员的时候默认带出业务员
              if (data.data.business_id) {
                this.$set(this.params, "business_id", data.data.business_id);
                this.employeeList = [
                  {
                    employee_id: data.data.business_id,
                    employee_name: data.data.business_name,
                  },
                ];
              }
            }
          });
      }
    },
    // 表体 计算页合计
    getSummaries(param) {
      const { columns, data } = param;
      const sums = []; // 页小计
      const fieldArr = ["total_price", "bill_receipt_price"];
      const _sum = function (field) {
        let sumprice = 0;
        if (data.length > 0) {
          sumprice = data.reduce(
            (total = 0, currentValue, currentIndex, arr) => {
              if (isNaN(Number(currentValue[field]))) {
                return total;
              }
              return total + Number(currentValue[field]);
            },
            0
          );
        }
        return sumprice;
      };
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "页小计";
          return;
        }
        if (fieldArr.includes(column["property"])) {
          sums[index] = this.$options.filters["priceFormatNone"](
            _sum(column["property"])
          );
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    // 初始化财务组织
    initDownData() {
      let _company_id = Number(localStorage.company_id);
      this.params = {};
      this.$set(this.params, "company_id", _company_id);
      this.$set(this.params, "auditor_status", 0);
      this.companyList = [
        {
          company_id: Number(localStorage.company_id),
          company_name: localStorage.company_name,
        },
      ];
    },
    // 点击重置
    onReset() {
      this.accountsTypeArr = []; // 往来类型初始化
      this.businessTimeValue = []; // 初始化业务日期
      this.auditorTimeValue = []; // 初始化审核日期
      this.billArr = []; // 初始化核销类型
      this.initDownData();
      this.listShow = false;
      Object.assign(this.params, {
        page: 1,
      });
      this.searchParams.params = this.params;
      this.portGetList(this.searchParams);
    },
    onSearch() {
      Object.assign(this.params, {
        page: 1,
      });
      this.searchParams.params = this.params;
      this.portGetList(this.searchParams);
    },
  },
  mounted() {
    this.initDownData();
    // 初始化加载显示列表
    this.onSearch(); // 使用查询方法显示
  },
};
</script>
