  methods: {
    // 刷新footer 合计行
    updateFooter() {
      this.$refs.xTable.updateFooter()
    },
    // 合并单元格
    rowspanMethod({
      row,
      $rowIndex,
      column,
      data
    }) {
      let fieldArr = ['company_name', 'business_time', 'business_sn', 'accounts_type_name', 'total_price']
      if (fieldArr.includes(column.property)) {
        if (row.rowspan || row.rowspan == 0) {
          return {
            rowspan: row.rowspan,
            colspan: 1
          }
        } else {
          return {
            rowspan: 1,
            colspan: 1
          }
        }
      }
    },
    // 合计方法
    footerMethod({
      columns,
      data
    }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "页小计";
          }
          if (
            ["bill_receipt_price", "total_price"].includes(
              column.property
            )
          ) {
            return this.$options.filters['priceFormatNone'](this.$utils.sum(data, column.property));
          }
          return null;
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (column.property == 'bill_receipt_price') { // 本单核销金额合计
            return this.TableData.all_bill_receipt_price
          } else if (column.property == 'total_price') {
            return this.TableData.all_total_price
          }
          return null;
        })
      ];
    }
  }
